<template>
  <label class="form-group">
    <input
      class="form-group__control"
      placeholder=" "
      :name="name"
      :value="value"
      @input="$emit('input', $event.target.value)"
    />

    <div class="form-group__label" v-if="label">{{ label }}</div>
  </label>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    },
    label: {
      type: String
    }
  }
}
</script>

<style scoped>
  .form-group {
    position: relative;
    height: 50px;
    border: 1px solid #ddd;
    border-radius: 2px;
    display: block;
    box-sizing: border-box;
    transition: border-bottom-color .2s;
  }
  .form-group:focus-within {
    border: 0;
    border-bottom: 2px solid hsl(var(--ui-active));
  }
  .form-group__control {
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    box-sizing: border-box;
    padding: 14px 15px 0 15px;
    font-size: 17px;
    font-family: inherit;
    color: hsl(var(--ui-primary-h), 20%, var(--ui-primary-l));
    box-shadow: inset 0 0 0 50px #fff;
    transition-property: border-color, padding, color;
    transition-duration: .2s;
  }
  .form-group__control:focus {
    color: hsl(var(--ui-active));
    padding: 15px 15px 0 0;
    border-radius: 0;
  }
  .form-group__label {
    position: absolute;
    top: 14px;
    left: 15px;
    font-size: 17px;
    cursor: text;
    color: hsl(var(--ui-primary-h), 20%, var(--ui-primary-l));
    transition: left .2s, top .2s, color .2s, font-size .2s;
  }
  .form-group__control:focus ~ .form-group__label {
    top: 5px;
    left: 0;
    font-size: 11px;
    color: hsl(var(--ui-active));
  }
  .form-group__control:not(:placeholder-shown) ~ .form-group__label {
    top: 5px;
    font-size: 11px;
  }
</style>